body {
  color: #fff;
  margin: 0;
}

.content {
  padding-bottom: 3em;
}

:root {
  --column-left: 7%;
  --column-width: 89%;
  hyphens: auto;
  --color-bg: #001168;
  --color-one: #1faef579;
  --color-two: #5ae4e486;
  --color-three: #ffa600;
  --color-signal: #0af;
  font-family: Work Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

@media (width >= 768px) {
  :root {
    --column-left: 25%;
    --column-width: 60%;
    font-size: 21px;
  }
}

@media (width >= 1100px) {
  :root {
    --column-left: 30%;
    --column-width: 50%;
    font-size: 21px;
  }
}

p {
  margin-top: 0;
  margin-bottom: .7em;
}

h1, h2, h3, h4 {
  margin-top: .9em;
  margin-bottom: .4em;
  line-height: 1.3;
}

h1 {
  color: var(--color-three);
  border-left: .3em solid var(--color-three);
  padding-left: .3em;
  font-family: IBM Plex Mono;
  font-size: 8vw;
  font-style: italic;
  font-weight: 400;
  line-height: 1.1;
}

@media (width >= 768px) {
  h1 {
    hyphens: none;
    font-size: 7vw;
    line-height: 1;
  }
}

h2 {
  letter-spacing: .4em;
  text-transform: uppercase;
  margin-top: 6em;
  margin-bottom: 2em;
  font-family: IBM Plex Mono;
  font-size: 1em;
  font-weight: 700;
}

h3 {
  border-left: .3em solid var(--color-signal);
  hyphens: none;
  color: var(--color-three);
  margin-top: 1.3em;
  padding-left: .3em;
  padding-right: 2em;
  font-family: IBM Plex Mono;
  font-size: 2rem;
  font-style: italic;
  font-weight: 400;
}

p {
  padding-left: 1.5em;
}

@media (width >= 768px) {
  p {
    padding-left: 4em;
  }
}

h4 {
  color: #fff;
  text-align: center;
  hyphens: none;
  font-size: 1.5rem;
}

.logo {
  letter-spacing: .4em;
  text-transform: uppercase;
  border: .4em solid #fff;
  border-radius: 2em;
  padding-left: .8em;
  padding-right: .6em;
  font-family: IBM Plex Mono;
  font-weight: 700;
}

.background {
  background: var(--color-bg);
  z-index: -1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

:not(.nodebox--header) > .node-text {
  width: var(--column-width);
  margin-left: var(--column-left);
}

.background:after {
  content: "";
  background-image: linear-gradient(to right, transparent 0, var(--color-bg) var(--column-left), var(--color-bg) calc(var(--column-left)  + var(--column-width)), transparent 100%);
  opacity: .7;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

ul, ol {
  margin: 1em 0;
  padding-left: 0;
  list-style: none;
}

li {
  margin-bottom: .7em;
}

a {
